import React, { useEffect, useRef, useState } from 'react';
import { useAppDispatch } from '../../../../../../renderer/store/hooks.js';
import CATEGORIES from '../../../../../../components/image/CATEGORY.js';
import { ADD_SELECTED_CATEGORY } from '../../../../../../renderer/_reducers/_slices/mapSlice.js';
import { CategoryItem } from '../../../../ModalGroup/CategoryFilterModal/CategoryFilterModal.js';
import { ApplyButton } from './ApplyButton.js';

const CATEGORY_LIST = Object.keys(CATEGORIES)
    .filter((key) => key !== 'Í∏∞ÌÉÄ')
    .sort();

export { CategoryFilter };

function CategoryFilter() {
    const checkboxRefs = useRef<Array<React.RefObject<HTMLInputElement>>>([]);

    useEffect(() => {
        // currentÏóê Î∞∞Ïó¥ÏùÑ Ìï†Îãπ(ref Î∞∞Ïó¥)
        checkboxRefs.current = Array(CATEGORY_LIST.length)
            .fill(null)
            .map(() => React.createRef<HTMLInputElement>());
    }, []);

    const handleUncheckAll = () => {
        // current Î∞∞Ïó¥ÏùÑ ÏàúÌöåÌïòÎ©¥ÏÑú check ÎÅî
        checkboxRefs.current.map((ref) => {
            if (ref.current !== null) {
                ref.current.checked = false;
            }
        });
    };

    return (
        <div className="container-filter">
            <em className="txt-filter-name">ÏóÖÏ¢Ö</em>
            <div>
                <form>
                    <ApplyButton />
                    <div className="container-button-all">
                        <button type="button" onClick={handleUncheckAll}>
                            Ï†ÑÏ≤¥ Ìï¥Ï†ú
                        </button>
                        <button type="reset">Ï†ÑÏ≤¥ ÏÑ†ÌÉù</button>
                    </div>
                    <div className="wrapper-checkbox-category reuse-in-result">
                        {CATEGORY_LIST.map((name, i) => {
                            return (
                                <CategoryItem key={Math.random()} name={name} index={i} ref={checkboxRefs.current[i]} />
                            );
                        })}
                    </div>
                </form>
            </div>
        </div>
    );
}
